background-color:  #fdfefe; */
.page-frame {
    margin: 0 auto;
    /* background-color: #008000; */
    background-color: #58184515;
    border-radius: 20px;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    box-sizing: border-box;
    
}

.header {
    background-color: var(--mainColor);
    color: white;
    /* justify-self: center; */
    width: 100%;
    height: fit-content;
    max-height: 120px;
    display: flex;
    align-items: center;
    padding: 20px 5px;
    border-radius: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    box-sizing: border-box;

}

.header-logo {
    width: 25%;
    display: flex;
    flex-direction: column;
}

.header-menu {
    width: 25%;
    margin-right: 5px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: auto;
}
.header-menu > div {
    margin: 5px;
}

.header-label {
    width: 50%;
    background-color: rgba(181, 168, 168, 0.137);
    text-align: center;
    border-radius: 10px;
}
.header-notification {
    
}

.contents {
    width: 100%;
    display: flex;
    height: 100%;
    overflow: auto;
    scrollbar-width: thin;
}



.footer {
    bottom: 0;
    background-color:  var(--mainColor2);
    color: var(--mainColor);
    border-top: 2px solid var(--mainColor);
    border-bottom: 2px solid var(--mainColor);
    /* position: absolute; */
    width: inherit;
    justify-content: center;
    display: flex;
    height: 100px;
    align-items: center;
    bottom: 0;
    border-radius: 5px;
}


.powered-div {
    display: block;
    position: absolute;
    right: 0;
    margin-right: 40px;
    margin-bottom: 45px;
    font-size: 12px;
    bottom: 0;
    opacity: .99;
    color: white;
    padding: 10px 20px;
    background-color: var(--mainColorB);
    border: 2px solid var(--mainColor);
    border-radius: 10px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: bold;
    transition: ease-in-out 1s;
}
.powered-div:hover {
    opacity: 2;
    transition: ease-in-out 1s;
