

.login {
    /* background-color: red; */
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* height: 100%; */
    border-radius: 8px;
}

.reg-login-title {
    display: inline-block;
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    padding: 10px;
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

