.login-input-frame-error {
    border: 1px solid var(--mainColorB);
    text-align: center;
}
.login-input-frame-error > span {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-style: italic;
    color: var(--mainColorB);
}

.login-input {
    padding: 20px 10px;
    display: block;
    text-align: center;
    width: 90%;
    border-radius: 10px;
    /* background-color: rgba(255, 0, 0, 0.159); */
}

.login-input > input {
    border: unset;
    width: 60%;
    text-align: center;
    line-height: 30px;
    background: transparent;
    color: var(--mainColor);
    border-bottom: 1.5px solid var(--mainColor);
    text-decoration: none;
}

.login-input > input:focus {
    border-bottom: 1px solid var(--mainColorB);
}

.clickable-btn-span {
    opacity: .5;
    color: var(--mainColor);
}
.clickable-btn-span:hover {
    opacity: 1;
}





.login-frame {
    margin: 0 auto;
    width: 800px;
    padding-top: 20px;

}


.login-reg-btn-wrapper {    
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    width: 100%;

}

